<template>
  <div class="err">
  	<el-row>
		  <el-col :span="5" class='list2'>
		  	<el-row>
		  		<el-form inline class='form'>
	          <el-form-item>
	            <el-select v-model="form.grade" size='mini' placeholder="">
	              <el-option label="" value=""></el-option>
	              <el-option label="" value=""></el-option>
	            </el-select>            
	          </el-form-item>
        	</el-form>
		  	</el-row>		  	
		  </el-col>
		  <el-col :span="5" class='list2'>
		  	<el-row>
		  		<el-form inline class='form'>
		  			<el-form-item>
		          <el-select v-model="form.clazz" size='mini' placeholder="">
	              <el-option label="" value=""></el-option>
	              <el-option label="" value=""></el-option>
	            </el-select>
            </el-form-item>
        	</el-form>
        </el-row>
		  </el-col>
		  <el-col :span="5" class='list2'>
		  	<el-row>
		  		<el-form inline class='form'>
	          <el-form-item>
	            <el-select v-model="form.tool" size='mini' placeholder="">
	              <el-option label="" value=""></el-option>
	              <el-option label="" value=""></el-option>
	            </el-select>
	          </el-form-item>
        	</el-form>
        </el-row>
		  </el-col>
		  <el-col :span="2" class='list2' offset="5">
		  	<el-form inline class='form'>
		  		<el-form-item>
            <el-button type='info' size='mini' @click="more1"></el-button>
          </el-form-item>
        </el-form>
		  </el-col>
		  <el-col :span="2" class='list2'>
		  	<el-form inline class='form'>
		  		<el-form-item>
            <el-button type='info' size='mini' @click="more2"></el-button>
          </el-form-item>
        </el-form>
		  </el-col>
		</el-row>
    <el-table :data="binds" style="width:100%;" class='table2' height='400' stripe :header-row-style="rowClass">
      <el-table-column type='index' label="" width="180"></el-table-column>
      <el-table-column prop="no" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="gender" label="">
      </el-table-column>
      <el-table-column prop="bindNo" label=""></el-table-column>
      <el-table-column label="" style="width:25%">
        <template slot-scope="scope">
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.user" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.passwd" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2"> </el-button>
        <el-button type="info" @click="submit2"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

export default {
  name: 'err',
  data(){
    return {
      title2:'',
      visible2:false,
      form2:{},
      form:{},
      binds:[{
        no: '20170101',
        name: '',
        gender:'',
        bindNo:'SH-1001',
      }, {
        no: '20170102',
        name: '',
        gender:'',
        bindNo:'SH-1001',
      }, {
        no: '20170103',
        name: '',
        gender:'',
        bindNo:'SH-1001',
      }],
    }
  },
  methods:{
  	more1() {
      this.$message({
        title: '',
        message: '',
        type: 'success'
      });
    },
    more2() {
      this.$message({
        title: '',
        message: '',
        type: 'success'
      });
    },
    edit2(){this.visible2 = true;},
    delete2(){
      this.$message({
        title: '',
        message: '',
        type: 'success'
      });
    },
    submit2(){
      this.$message({
        title: '',
        message: '',
        type: 'success'
      });
      this.visible2 = false;
    },
    cancle2(){this.visible2 = false;},
    rc2({row,column,rowIndex,columnIndex}){//
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1,2];
        } else if (columnIndex === 1) {
          return [0,0];
        }
      }
    },
    rc({row,column,rowIndex,columnIndex}){
      if (columnIndex===0||columnIndex===1||columnIndex===2){  //1230
        if (rowIndex%2===0) {//
          return [2,1];
        } else {
          return [0,0];
        }
      }
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .font{
    display:inline-block;
    line-height:30px;
  }
  .td{
    height:80px;
    border:1px solid black;
    margin-left:10px;
    text-align:center;
    padding:10px;
    margin:5px;
  }
  .content{
    border:1px solid #999;
    box-shadow:6px 6px 12px #999;
    padding:10px 0;
    height:480px;
    overflow:auto;
  }
  .errStu{
    height:230px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
  }
  .getInfo{
    height:200px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
  }
  .query:hover{
    text-decoration:underline;
    color:red;
    cursor:pointer;
  }
  .title{
    margin-top:20px;
    font-size:20px;
  }
  .table2{
    overflow:auto;
  }
  .list2 .el-select{
    width:100%;
  }
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
</style>



