<template>
  <div class="curve">
    <div>
      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple">
            <el-form :inline="true" :data="form" class="demo-form-inline form" size='mini'>
              <el-row>
                <el-col :span='8'>
                  <el-form-item>
                    <el-col :span="11">
                      <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
                    </el-col>
                    <el-col :span="2">
                      <el-row type='flex' justify='center'>-</el-row>
                    </el-col>
                    <el-col :span="11">
                      <el-date-picker type="date" placeholder="" v-model="form.date2" style="width: 100%;"></el-date-picker>
                    </el-col>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-select v-model="form.grade" placeholder="" >
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-select v-model="form.clazz" placeholder="" >
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-select v-model="form.course" placeholder=''> 
                      <el-option label=" 1600-16:45" value=" 1600-16:45"></el-option>
                      <el-option label=" 1000-10:20" value=" 1000-10:20"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-button type="text" size="medium" @click='remark'></el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>
        </el-col> 
        <el-col :span="5"></el-col>  
      </el-row>
      <el-row>
        <el-col :span='19'>
          <el-row>
            <el-col :span='10'>
              <el-row>
                <el-col :span='8' class='font'><a>138</a></el-col>
                <el-col :span='8' class='font'><a>75</a></el-col>
                <el-col :span='8' class='font'><a>95</a></el-col>
              </el-row>
            </el-col>
            <el-col :span='3' class='font'><a>95</a></el-col>
            <el-col :span='3' class='font'><a>105</a></el-col>
            <el-col :span='3' class='font'><a>115</a></el-col>
            <el-col :span='5'>
              <el-row type='flex'>
                <el-select v-model="form.rate" size='mini' placeholder="" @change='change'>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-row>
            </el-col>
          </el-row>
          <el-row class='container'>
            <div class="two">
            <!-- 1.  -->
              <div id="main" style="height:400px;"></div>
              <div id="main2" style="height:400px;"></div>
            </div>
          </el-row>
        </el-col>
        <el-col :span="5">
          <el-row type='flex' justify='end' id='right2'>
            <el-col :span='22'>
              <el-row>
                <el-col :span='16'></el-col>
              </el-row>
              <el-row class='stuList'>
                <el-col :span='24'>
                  <el-row class='height'>
                    <el-col :span='12'><b class='primary'></b></el-col>
                    <el-col :span='12'><b></b></el-col>
                  </el-row>
                  <el-row class='height'>
                    <el-col :span='12'><b></b></el-col>
                    <el-col :span='12'><b></b></el-col>
                  </el-row>
                  <el-row class='height'>
                    <el-col :span='12'><b></b></el-col>
                    <el-col :span='12'><b></b></el-col>
                  </el-row>
                  <el-row class='height'>
                    <el-col :span='12'><b></b></el-col>
                    <el-col :span='12'><b></b></el-col>
                  </el-row>
                </el-col>
              </el-row>
            </el-col> 
          </el-row> 
        </el-col>  
      </el-row>
    </div>
  </div>
</template>

<script>
//2. echarts
let echarts = require('echarts');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
import $ from 'jquery';

var bool = false;
export default {
  name: 'curve',  
  data(){
    return{
      courseTime:'',
      formInline:'',
      form:{rate:''},
      student:'',
    }
  },
  mounted(){
    $('.stuList .height b').click(function(){
      $('.stuList .height b').removeAttr('class');
      $(this).attr('class','primary');
      this.student = $('this').text();
    });
    this.move1();
    this.move2();
    this.ifBool();
  },
  methods:{
    change(){
      bool = !bool;
      this.ifBool();
    },
    ifBool(){
      if(bool == false){
        $('#main2').css('display','none');
        $('#main').css('display','block');
        $('#right2').css('visibility','hidden');
        this.form.rate = '';
      }else{
        $('#main').css('display','none');
        $('#main2').css('display','block');
        $('#right2').css('visibility','visible');
        this.form.rate = '';
      }
    },
    curvePicture(){
      var colors = ['green','blue','red'];
      var arr = [],arr1 = [],arr2 = [],arr3 = [];
      var arrx = [],arry1 = [],arry2 = [],arry3 = [];
      var start = 0,end = 31;
      var rateText = '';
      var rateSubtext = ' 1 ';
      var myChart = echarts.init($('#main')[0]);
      for(var k=0;k<=602;k=k+2){
        arr.push(k);
      }
      for(var k=0;k<301;k++){
        var random1 = Math.floor(Math.random()*199+1);
        var random2 = Math.floor(Math.random()*199+1);
        var random3 = Math.floor(Math.random()*199+1);
        arr1.push(random1);
        arr2.push(random2);
        arr3.push(random3);
      }
      function sw(){
        arrx = [],arry1 = [],arry2 = [],arry3 = [];
        for(var i=start;i<end;i++){
          arrx.push(arr[i]);
          arry1.push(arr1[i]);
          arry2.push(arr2[i]);
          arry3.push(arr3[i]);
        }
      }
      function time(){//
        var date = new Date();
        date = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" &nbsp; "+repair(date.getHours())+':'+repair(date.getMinutes());
        return date;
      }
      function repair(n){//0
        n = n + '';
        return '00'.slice(0,2-n.length)+n;
      }
      // 
      function curve(){
        if(end<arr.length){
          sw();
          start++;
          end++;
          var option = {
            color: colors,
            title:{
              text:rateText,
              left:'center',
              top:'10',
              subtext:rateSubtext,
              subtextStyle:{
                color:'#aaa',
                fontWeight:'bolder',
                align:'left',
              },
            },
            tooltip: {
              show:true,
              trigger:'axis',
              axisPointer:'shadow',
              snap:'snap',
              formatter: function(params){
                var date = time();
                var str = date + '<br/>' + '' + params[0].value + '/<br/>' + '' + params[1].value + '/<br/>' + '' + params[2].value + '/';
                return str;
              },
              textStyle:{//
                color:['white'],
              },
            },
            legend: {
              data:['','',''],
              x:'right',
              top:35,
            },
            calculable : true,
            grid: {  //
              top: 120,
              bottom: 50,
            },
            xAxis: [{  //X
              name:'/',
              type: 'category',
              boundaryGap : false, //
              data: arrx,
            }],
            yAxis: [{
              name:'/',
              type: 'value',
            }],
            series: [{
              name:'',
              type:'line',
              data: arry1,
              markPoint : {
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:''}
                ]
              },
            },{    
              name:'',
              type:'line',
              data: arry2,
              markPoint : {
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:''}
                ]
              },
            },{
              name:'',
              type:'line',
              data: arry3,
              markPoint : {
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:''}
                ]
              },
            }],
          }
          myChart.setOption(option);
        }
      }
      return curve;
    },
    curvePicture2(){
      var colors = ['#3300ff','#00ff99'];
      var myChart = echarts.init(document.getElementById('main2'));
      var arr = [],arr1 = [],arr2 = [];
      var arrx = [],arry1 = [],arry2 = [];
      var start = 0,end = 31;
      for(var k=0;k<=602;k=k+2){
        arr.push(k);
      }
      for(var k=0;k<301;k++){
        var random1 = Math.floor(Math.random()*199+1);
        var random2 = Math.floor(Math.random()*199+1);
        arr1.push(random1);
        arr2.push(random2);
      }
      function sw(){
        arrx = [],arry1 = [],arry2 = [];
        for(var i=start;i<end;i++){
          arrx.push(arr[i]);
          arry1.push(arr1[i]);
          arry2.push(arr2[i]);
        }
      }
      function time(){//
        var date = new Date();
        date = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" &nbsp; "+repair(date.getHours())+':'+repair(date.getMinutes());
        return date;
      }
      function repair(n){//0
        n = n + '';
        var str = '00';
        return str.slice(0,2-n.length)+n;
      }
      // 
      var vm = this;
      function curve(){
        if(end<arr.length){
          sw();
          start++;
          end++;
          var option = {
            color: colors,
            title:{
              text:'',
              left:'center',
              top:'10',
              subtext:'  web1701  '+vm.student,
              subtextStyle:{
                color:'#aaa',
                fontWeight:'bolder',
                align:'left'
              },
            },
            tooltip: {
              show:true,
              trigger:'axis',
              axisPointer:'shadow',
              snap:'snap',
              formatter: function(params){
                var date = time();
                var str = date + '<br/>' + '' + params[0].value + '/<br/>' + '' + params[1].value + '/';
                return str;
              },
              textStyle:{//
                color:['white'],
              },
            },
            legend: {
              data:['',''],
              x:'right',
              top:35,
            },
            calculable : true,
            grid: {  //
              top: 120,
              bottom: 50,
            },
            xAxis: [{  //X
              name:'/',
              type: 'category',
              boundaryGap : false, //
              data: arrx,
            }],
            yAxis: [{
              name:'/',
              type: 'value',
            }],
            series: [{
              name:'',
              type:'line',
              data: arry1,
              markPoint : {
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:''}
                ]
              },
            },{    
              name:'',
              type:'line',
              data: arry2,
              markPoint : {
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:''}
                ]
              },
            }],
          }
          myChart.setOption(option);
        }
      }
      return curve;
    },
    move1(){
      var curve = this.curvePicture();
      setInterval(function(){
        curve();
      },2000);
      curve();//
    },
    move2(){
      var curve = this.curvePicture2();
      setInterval(function(){
        curve();
      },2000);
      curve();//
    },
    remark(){
      this.$message({
        message:'',
        type:'success',
      });
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    margin:0;
    padding:0;
    font-size:12px;
  }
  .form{
    height:38px;
  }
  .font{
    display:inline-block;
    height:30px;
    line-height:30px;
    font-size:12px;
  }
  #remark{
    margin-top: 5px;
  }
  .container .two{
    border: 1px solid black;
    margin-top: 20px;
    box-shadow: 10px 10px 10px #999;
  }
  .stuList{
    height:404px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
    margin-top:5px;
  }
  #right2{
    margin-top:30px;
  }
  .height{
    height:30px;
    line-height:30px;
    text-align:center;
  }
  .primary{
    background:#169bd5;
    color:white;
    border-radius:4px;
  }
  .stuList .el-row .el-col{
    cursor:pointer;
  }
  .stuList .el-row .el-col b{
    display:inline-block;
    width:100%;
  }
  
</style>



