<template>
	<div class="data">
		<el-row>
      <el-col :span='24'>
      	<el-form :inline="true" :data="form" class="demo-form-inline" size='mini' >
	      	<el-row :span='24' >
	      	  <el-col :span='5'>
		          <el-form-item label=""  width='100%'>
		            <el-date-picker size='mini' v-model="form.time" type="date" placeholder=""></el-date-picker>
		          </el-form-item>
	          </el-col>
	          <el-col :span='4'>
		          <el-form-item label="" :inline="true">
		            <el-select v-model="form.grade" placeholder="" >
		              <el-option label="" value=""></el-option>
		              <el-option label="" value=""></el-option>
		            </el-select>
		          </el-form-item>
	          </el-col>
	          <el-col :span='4'>
		          <el-form-item label="" :inline="true">
		            <el-select v-model="form.clazz" placeholder="" >
		              <el-option label="" value=""></el-option>
		              <el-option label="" value=""></el-option>
		            </el-select>
		          </el-form-item>
	          </el-col>
	          <el-col :span='4'>
		          <el-form-item label="" :inline="true">
		            <el-select v-model="form.course" placeholder=''> 
		              <el-option label=" 1600-16:45" value=" 1600-16:45"></el-option>
		              <el-option label=" 1000-10:20" value=" 1000-10:20"></el-option>
		            </el-select>
		          </el-form-item>
			      </el-col>
			      <el-col :span='6'>
		          <el-form-item>
		            <el-button type='info' size='mini' :inline="true"></el-button>
		          </el-form-item>
	          </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
    	<el-col :span='24'>
    		<el-row type='flex' justify='center'>
    			<span class='title'></span>
    			<el-row type='flex' justify='end'>
	    			<el-form>
	    				<el-form-item>
	    					<el-button type='info' size='mini'></el-button>
	    				</el-form-item>
	    			</el-form>
    			</el-row>
    		</el-row>
    	</el-col>
    </el-row>
		<el-table :data="classData" style="width:100%;" height='430' size='mini' stripe :header-row-style="rowClass">
	    <el-table-column fixed prop="class" label="">
	    </el-table-column>
	    <el-table-column prop="date" label="">
	    </el-table-column>
	    <el-table-column prop="teacher" label="">
	    </el-table-column>
	    <el-table-column prop="students" label="">
	    </el-table-column>
	    <el-table-column prop="lowRate" label="">
	    </el-table-column>
	    <el-table-column prop="averageRate" label="">
	    </el-table-column>
	    <el-table-column prop="heightRate" label="">
	    </el-table-column>
	    <el-table-column prop="closeLowStu" label="">
	    </el-table-column>
	    <el-table-column prop="closeHeightStu" label="">
	    </el-table-column>
	    <el-table-column prop="overLowRate" label="">
	    </el-table-column>
	    <el-table-column prop="overHeightRate" label="">
	    </el-table-column>
	    <el-table-column prop="lesson" label="">
	    </el-table-column>
	  </el-table>
	</div>
</template>

<script type="text/javascript">
export default {
  name: 'data',
  data () {
    return {
    	form:{},
      classData: [{
	      class:'1',
	      date: '2017120114:30~15:15',
	      teacher: '',
	      students: '45',
	      lowRate: '50',
	      averageRate:'79',
	      heightRate: '102',
	      closeLowStu: '2',
	      closeHeightStu:'1',
	      overLowRate:'1',
	      overHeightRate:'1',
	      lesson:''
	    },{
	      class:'2',
	      date: '2017120114:30~15:15',
	      teacher: '',
	      students: '45',
	      lowRate: '50',
	      averageRate:'79',
	      heightRate: '102',
	      closeLowStu: '2',
	      closeHeightStu:'1',
	      overLowRate:'1',
	      overHeightRate:'1',
	      lesson:''
	    }],
    }
  },
  methods:{
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },

  },
}
</script>

<style scoped>
	*{
		font-size:12px;
	}
	.title{
		font-size:20px;
		margin:20px auto;
	}
  
</style>



