<template>
  <div class="err">
  	<el-row>
		  <el-col :span="5">
		  	<el-row>
		  		<el-form inline class='form'>
	          <el-form-item>
	            <el-select v-model="form.grade" size='mini' placeholder="">
	              <el-option label="" value=""></el-option>
	              <el-option label="" value=""></el-option>
	            </el-select>            
	          </el-form-item>
        	</el-form>
		  	</el-row>		  	
		  </el-col>
		  <el-col :span="5">
		  	<el-row>
		  		<el-form inline class='form'>
		  			<el-form-item>
		          <el-select v-model="form.clazz" size='mini' placeholder="">
	              <el-option label="" value=""></el-option>
	              <el-option label="" value=""></el-option>
	            </el-select>
            </el-form-item>
        	</el-form>
        </el-row>
		  </el-col>
		  <el-col :span="5">
		  	<el-row>
			  	<el-form inline class='form'>
			  		<el-form-item>
	            <el-select v-model="form.course" size='mini' placeholder="">
	              <el-option label="" value=""></el-option>
	              <el-option label="" value=""></el-option>
	            </el-select>
	          </el-form-item>
	        </el-form>
        </el-row>
		  </el-col>
		</el-row>
    <!--<el-row>
      <el-col :span='19'>
        <el-form inline class='form'>
          <el-form-item label=''>
            <el-select v-model="form.grade" size='mini' placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
            <el-select v-model="form.clazz" size='mini' placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=''>
            <el-select v-model="form.course" size='mini' placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span='5'></el-col>
    </el-row>-->
    <el-row>
      <el-col :span='19'>
        <el-row>
          <el-col :span='10'>
            <el-row>
              <el-col :span='8' class='font'><a>138</a></el-col>
              <el-col :span='8' class='font'><a>75</a></el-col>
              <el-col :span='8' class='font'><a>95</a></el-col>
            </el-row>
          </el-col>
          <el-col :span='3' class='font'><a>95</a></el-col>
          <el-col :span='3' class='font'><a>105</a></el-col>
          <el-col :span='3' class='font'><a>115</a></el-col>
          <el-col :span='5'>
            <el-row type='flex' justify='end'>
              <el-select v-model="form.rate" size='mini' placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span='5'></el-col>
    </el-row>
    <el-row type='flex' justify='center'>
      <span class='title'></span>
    </el-row>
    <el-row>
      <el-col :span='12'>
        <span><a>3</a></span>
        <span><a>4</a></span>
      </el-col>
      <el-col :span='12'>
        <el-row type='flex' justify='end'>
          <el-button size='mini' @click='download'></el-button>
          <el-button size='mini' @click='back'></el-button>
        </el-row>
      </el-col>
    </el-row>
    <el-table :data="errs" :span-method="rc" style="width: 100%;" class='table2' height='410' stripe :header-row-style="rowClass">
      <el-table-column prop="no" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="gender" label="">
      </el-table-column>
      <el-table-column prop="errState" label=""></el-table-column>
      <el-table-column prop="errStart" label=""></el-table-column>
      <el-table-column prop="errSeries" label=""></el-table-column>
      <el-table-column prop="course" label=""></el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
  name: 'err',
  data(){
    return {
      form:{},
      errs:[{
        no: '20170101',
        name: '',
        gender:'',
        errState:'',
        errStart:'14:37:35',
        errSeries:'4',
        course:'',
        content:'',
      }, {
        no: '20170102',
        name: '',
        gender:'',
        errState:'',
        errStart:'14:37:35',
        errSeries:'4',
        course:'',
        content:'',
      }, {
        no: '20170103',
        name: '',
        gender:'',
        errState:'',
        errStart:'14:37:35',
        errSeries:'4',
        course:'',
        content:'',
      }],
    }
  },
  methods:{
    rc2({row,column,rowIndex,columnIndex}){//
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1,2];
        } else if (columnIndex === 1) {
          return [0,0];
        }
      }
    },
    rc({row,column,rowIndex,columnIndex}){
      if (columnIndex===0||columnIndex===1||columnIndex===2){  //1230
        if (rowIndex%2===0) {//
          return [2,1];
        } else {
          return [0,0];
        }
      }
    },
    download(){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    back(){
      this.$message({
        message: '',
        type: 'error',
      });
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .form{
    height:38px;
  }
  .font{
    display:inline-block;
    line-height:30px;
  }
  .td{
    height:80px;
    border:1px solid black;
    margin-left:10px;
    text-align:center;
    padding:10px;
    margin:5px;
  }
  .content{
    border:1px solid #999;
    box-shadow:6px 6px 12px #999;
    padding:10px 0;
    height:480px;
    overflow:auto;
  }
  .errStu{
    height:230px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
  }
  .getInfo{
    height:200px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
  }
  .query:hover{
    text-decoration:underline;
    color:red;
    cursor:pointer;
  }
  .title{
    margin-top:20px;
    font-size:20px;
  }
  .table2{
    overflow:auto;
  }

</style>



