<!--  -->
<template>
	<div class="courseSetting">
		<el-row>
			<el-col :span='16'>
        <el-form>
          <el-form-item label=''>
            <el-select v-model="course" placeholder="" size='mini'>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-form>
			</el-col>
			<el-col :span='8'>
				<el-row type='flex' justify='end'>
					<el-button type='info' size='small' @click='add2'></el-button>
					<el-button type='info' size='small' @click='preserve2'></el-button>
				</el-row>
			</el-col>
		</el-row>
		<el-table :data="courseData" style="width: 100%" height='450' class='table' stripe :header-row-style="rowClass">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="lessionType" label=""></el-table-column>
      <el-table-column prop="lessionName" label=""></el-table-column>
      <el-table-column prop="state" label=""></el-table-column>
      <el-table-column label="">
	      <template slot-scope="scope">
	        <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
	      </template>
	    </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-select v-model="form2.lessonType" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.lessonName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2"> </el-button>
        <el-button type="info" @click="submit2"> </el-button>
      </div>
    </el-dialog>
    <el-dialog :title="title22" :visible.sync="visible22" center>
      <el-table :data="courseData22" style="width:100%" height="250">
        <el-table-column type="index" label="" style="width:20%"></el-table-column>
        <el-table-column prop="lessonType" label="" style="width:20%">
        </el-table-column>
        <el-table-column label="" min-width='260'>
          <template slot-scope='scope'>
            <!-- <el-button size="mini" @click="edit22(scope.$index,scope.row)"></el-button> -->
            <el-button size="mini" type="success" @click="add22(scope.$index,scope.row)"></el-button>
            <el-button size="mini" @click="edit22(scope.$index,scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="delete22(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle22"> </el-button>
        <el-button type="info" @click="submit22"> </el-button>
      </div>
    </el-dialog>

	</div>
</template>

<script type="text/javascript">
export default {
  name: 'courseSetting',
  data(){
    return {
      fw:'120px',
      title2:'',
      title22:'',
      course:'',
      form2:{},
      form22:{},
      visible2:false,
      visible22:false,
      courseData: [{
        lessionType:'',
        lessionName: '',
        state:'',
      },{
        lessionType:'',
        lessionName: '',
        state:'',
      },{
        lessionType:'',
        lessionName: '',
        state:'',
      }],
      courseData22: [{
        lessonType:'',
      },{
        lessonType:'',
      },{
        lessonType:'',
      }],
    }
  },
  methods:{
    add2(){this.visible2=true;this.title2='';},
    add22(){},
    preserve2(){this.visible22=true;},
    edit2(i,row){
    //  console.log(i,row);
      this.visible2=true;this.title2='';
    },
    edit22(i,row){
      
    },
    delete2(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    delete22(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    submit2(i,row){this.visible2=false;
      this.$message({
        message: '',
        type: 'success',
      });},
    submit22(i,row){this.visible22=false;
      this.$message({
        message: '',
        type: 'success',
      });},
    cancle2(){this.visible2=false;},
    cancle22(){this.visible22=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  },
}
</script>

<style scoped>
	.icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


