<!--  -->
<template>
	<div class="content">
		<!--  -->
		<div class="tbl-header">
			<el-tabs v-model="activeName" type="card" @tab-click="tabClick">
		    <el-tab-pane label="" name="teacher"></el-tab-pane>
		    <el-tab-pane label="" name="student"></el-tab-pane>
		  </el-tabs>
		</div>
		<!--  -->
	  <div class="tbl-content">
	  	<router-view></router-view>
	  </div>
	</div>
</template>
<script>
import path from 'path';
export default {
 	data(){
		return {
			name:'',
      activeName:'teacher',
		}
	},
	mounted(){//
		this.$router.push({
      path:path.join('/settings/users/','teacher'),
    });
	},
  methods:{
  	// 
    tabClick(tab, event) {
      this.$router.push({
        path:path.join('/settings/users/',tab.name),
      });
    }
  }
}
</script>

<style scoped>
	
</style>


