<script>
let echarts = require('echarts');
require('echarts/lib/component/tooltip');

export default {
  name: 'clazzRate',
  data(){
    return {
      form:{},
    }
  },
  methods:{
  	open1(){
        this.$message({
          title: '',
          message: '',
          type: 'success'
        });
      },
  	open2(){
        this.$message({
          title: '',
          message: '',
          type: 'success'
        });
     },
  	open3(){
        this.$message({
          title: '',
          message: '',
          type: 'success'
        });
     },
     open4(){
        this.$message({
          title: '',
          message: '',
          type: 'success'
        });
      }
  
  },
  mounted(){
    var colors = ['#3300ff','#00ff99','#ffcc00'];
    var myChart = echarts.init(document.getElementById('main'));
    var arr = [],arr1 = [],arr2 = [],arr3 = [];
    var arrx = [],arry1 = [],arry2 = [],arry3 = [];
    var start = 0,end = 31;
    for(var k=0;k<=602;k=k+2){
      arr.push(k);
    }
    for(var k=0;k<301;k++){
      var random1 = Math.floor(Math.random()*199+1);
      var random2 = Math.floor(Math.random()*199+1);
      var random3 = Math.floor(Math.random()*199+1);
      arr1.push(random1);
      arr2.push(random2);
      arr3.push(random3);
    }
    function sw(){
      arrx = [],arry1 = [],arry2 = [],arry3 = [];
      for(var i=start;i<end;i++){
        arrx.push(arr[i]);
        arry1.push(arr1[i]);
        arry2.push(arr2[i]);
        arry3.push(arr3[i]);
      }
    }
    function time(){//
      var date = new Date();
      date = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" &nbsp; "+repair(date.getHours())+':'+repair(date.getMinutes());
      return date;
    }
    function repair(n){//0
      n = n + '';
      var str = '00';
      return str.slice(0,2-n.length)+n;
    }
    // 
    function curve(){
      if(end<arr.length){
        sw();
        start++;
        end++;
        var option = {
          color: colors,
          title:{
            text:'',
            left:'center',
            top:'10',
            subtext:'  1   ',
            subtextStyle:{
              color:'#aaa',
              fontWeight:'bolder',
              align:'left'
            },
          },
          tooltip: {
            show:true,
            trigger:'axis',
            axisPointer:'shadow',
            snap:'snap',
            formatter: function(params){
              var date = time();
              var str = date + '<br/>' + '' + params[0].value + '/<br/>' + '' + params[1].value + '/<br/>' + '' + params[2].value + '/';
              return str;
            },
            textStyle:{//
              color:['white'],
            },
          },
          legend: {
            data:['','',''],
            x:'right',
            top:35,
          },
          calculable : true,
          grid: {  //
            top: 120,
            bottom: 50,
          },
          xAxis: [{  //X
            name:'/',
            type: 'category',
            boundaryGap : false, //
            data: arrx,
          }],
          yAxis: [{
            name:'/',
            type: 'value',
          }],
          series: [{
            name:'',
            type:'line',
            data: arry1,
            markPoint : {
              data : [
                {type : 'max', name: ''},
                {type : 'min', name: ''}
              ]
            },
            markLine : {
              data : [
                {type :'average', name:''}
              ]
            },
          },{    
            name:'',
            type:'line',
            data: arry2,
            markPoint : {
              data : [
                {type : 'max', name: ''},
                {type : 'min', name: ''}
              ]
            },
            markLine : {
              data : [
                {type :'average', name:''}
              ]
            },
          },{
            name:'',
            type:'line',
            data: arry3,
            markPoint : {
              data : [
                {type : 'max', name: ''},
                {type : 'min', name: ''}
              ]
            },
            markLine : {
              data : [
                {type :'average', name:''}
              ]
            },
          }],
        }
        myChart.setOption(option);
      }
    }
    setInterval(function(){
      curve();
    },2000);
    curve();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .form{
    height:44px;
  }
  .font{
    display:inline-block;
    line-height:30px;
  }
  .height{
    height:30px;
    line-height:30px;
    margin-left:10px;
  }
  .td{
    height:80px;
    border:1px solid black;
    margin-left:10px;
    text-align:center;
    padding:10px;
    margin:5px;
  }
  .content{
    margin-top:10px;
    border:1px solid #999;
    box-shadow:6px 6px 12px #999;
    padding:10px 0;
    height:450px;
    overflow:auto;
  }
  .errStu{
    height:230px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
    margin-top:5px;
  }
  .getInfo{
    height:200px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-top:5px;
  }
  .query:hover{
    text-decoration:underline;
    color:red;
    cursor:pointer;
  }
	.el-select{
    width:100%;
  }
</style>



