<template>
	<div class="list">
		<el-row>
		  <el-col :span="12">
			 	<template>
				  <el-select v-model="value" placeholder="1" size='small'>
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
				</template>
		  </el-col>
		  <el-col :span="12">
			 	<el-row type='flex' justify='end'>
          <el-button type="info" @click='single2' size='small'></el-button>
          <el-button type="info" @click='batch2' size='small'></el-button>
        </el-row>
		  </el-col>
		</el-row>	 
    <div class='coll2'></div>
    
		<el-table :data="lists" style="width:100%" stripe :header-row-style="rowClass">
			<el-table-column type="selection">
    	</el-table-column>
			<el-table-column type='index' label="">
      </el-table-column>
      <el-table-column prop="no" label="">
      </el-table-column>
      <el-table-column prop="state" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>
    <div class='coll'></div>
		<el-row>
			<el-col :span="8">
			 	<el-button type="info" size='small'></el-button>
			</el-col>
			<el-col :span="16">
			 	<el-row type='flex' justify='end'>
        	<el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page="currentPage4"
			      :page-sizes="[100, 200, 300, 400]"
			      :page-size="100"
			      layout="total, sizes, prev, pager, next, jumper"
			      :total="400">
			    </el-pagination>
        </el-row>
			</el-col>
		</el-row>
	
		<el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.num" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2"> </el-button>
        <el-button type="info" @click="submit2"> </el-button>
      </div>
    </el-dialog> 
    <el-dialog :title="title2s" :visible.sync="visible2s" center>
      <el-form :model="form2s" class='dialog3' size='small'>
        <el-form-item label="" :label-width="fw3">
        	<el-input placeholder="" suffix-icon="el-icon-document" v-model="form2s.file"></el-input>
        </el-form-item>
      	<el-form-item label="" :label-width="fw3">
        	<el-table :data="lists" style="width:100%">
			      <el-table-column prop="name" label="" width="200">
			      </el-table-column>
			      <el-table-column prop="no" label="" width="120">
			      </el-table-column>
		    </el-table>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2s"> </el-button>
        <el-button type="info" @click="submit2s"> </el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script>
	export default {
    data() {
      return {
      	fw:'120px',
      	fw3:'170px',
      	currentPage4: 4,
      	value:'',
        title2:'',
      	title2s:'',
        visible2:false,
      	visible2s:false,
        form2:{},
      	form2s:{},
      	lists:[{
      		no:'SH1001',
      		state:'',
      	},{
      		no:'SH1002',
      		state:'',
      	}],
        options: [{
          value: '1',
          label: '1',
        }, {
          value: '2',
          label: '2',
        }],
      }
    },
     methods: {
     	edit2(i,row){this.visible2=true;this.title2='';},
    	delete2(i,row){
        this.$message({
          message: '',
          type: 'success',
        });
      },
    	single2(){this.visible2=true;this.title2='';},
    	batch2(){this.visible2s=true;},
    	submit2(i,row){
        this.$message({
          message: '',
          type: 'success',
        });
        this.visible2=false;
      },
      submit2s(i,row){
        this.$message({
          message: '',
          type: 'success',
        });
        this.visible2s=false;
      },
      cancle2(){this.visible2=false;},
	    cancle2s(){this.visible2s=false;},
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      rowClass(row, index) {
        return {"background-color":"#A5B0BB",'color':'white'}
      },
    },
  }
</script>

<style scoped>
	.icon {
    cursor: pointer;
    font-size: 20px;
  }
  .coll{
  	height:240px;
  }
  .coll2{
    height:20px;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog3 .el-input{
    width:300px;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


