<!--   -->
<template>
	<div class="teacher">
		<el-row>
			<el-col :span='14'>
				<el-form inline>
          <el-row>
            <el-col :span='6'>
              <el-form-item>
                <el-input v-model='keys' size='mini' placeholder=''></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-button type='info' size='mini'></el-button>
              </el-form-item>
            </el-col>
        </el-row>
				</el-form>
			</el-col>
			<el-col :span='10'>
				<el-row type='flex' justify='end'>
          <el-form inline>
            <el-row>
              <el-col :span='8'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='single2'></el-button>
                </el-form-item>
              </el-col>
              <el-col :span='8'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='admin2'></el-button>
                </el-form-item>
              </el-col>
              <el-col :span='8'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='batch2'></el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="teacher" style="width: 100%" height="426" class='table' :header-row-style="rowClass">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" label=''></el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="user" label="">
      </el-table-column>
      <el-table-column prop="phone" label="">
      </el-table-column>
      <el-table-column prop="gender" label="">
      </el-table-column>
      <el-table-column prop="state" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
          
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.user" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.passwd" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form2.gender" label=""></el-radio>
          <el-radio class="radio" v-model="form2.gender" label=""></el-radio>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2"> </el-button>
        <el-button type="info" @click="submit2"> </el-button>
      </div>
    </el-dialog>

    <el-dialog :title="title2s" :visible.sync="visible2s" center>
      <el-form :model="form2s" class='dialog2' size='small'>
        <el-form-item label="" :label-width="fw">
          <el-input placeholder="" suffix-icon="el-icon-document" v-model="form2s.file"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2s"> </el-button>
        <el-button type="info" @click="submit2s"> </el-button>
      </div>
    </el-dialog> 

    <el-dialog :title="titleAdmin" :visible.sync="visibleAdmin" center>
      <el-form :model="formAdmin">
        <a></a><el-checkbox v-model="formAdmin.survey"></el-checkbox><hr color='#eee'>
        <a></a><el-checkbox v-model="formAdmin.data"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.curve"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.clazzData"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.err"></el-checkbox><hr color='#eee'>
        <a></a><el-checkbox v-model="formAdmin.set"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.unit"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.user"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.table"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.rate"></el-checkbox><hr color='#eee'>
      </el-form>
      <div slot="footer" class="dialog-footer">
         <el-button @click="cancleAdmin"> </el-button>
         <el-button type="info" @click="submitAdmin"> </el-button>
      </div>
    </el-dialog>  
	</div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';

export default {
  name: 'teacher',
  data(){
    return {
      fw:'120px',
    	keys:'',
      title2:'',
      title2s:'',
      titleAdmin:'',
      form2:{},
      form2s:{},
      formAdmin:{},
      visible2:false,
      visible2s:false,
      visibleAdmin:false,
    }
  },
  created(){
    this.findAllTch();
  },
  computed:{
    ...mapGetters(['teacher']),
  },
  methods:{
    edit2(i,row){
      this.visible2=true;
      this.title2='';
      this.form2 = row;
    },
    delete2(i,row){
      var id = row.id;
      this.deleteByIdTch(id).then((result)=>{
        this.$message({
          type: 'success',
          message: '',
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '',
        });
      });
    },
    single2(){
      this.visible2=true;
      this.title2='';
      this.form2 = row;
    },
    batch2(){this.visible2s=true;},
    admin2(){this.visibleAdmin=true;this.titleAdmin='';},
    submit2(){
      this.saveTch(this.form2).then((result)=>{
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: ''
        });
      });
      this.visible2=false;
    },
    submit2s(){
      this.saveTch(this.form2).then((result)=>{
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: ''
        });
      });
      this.visible2s=false;
    },
    submitAdmin(i,row){
      this.saveTch(this.formAdmin).then((result)=>{
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: ''
        });
      });
      this.visibleAdmin=false;
    },
    cancle2(){this.visible2=false;},
    cancle2s(){this.visible2s=false;},
    cancleAdmin(){this.visibleAdmin=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
    ...mapActions(['findAllTch','saveTch','deleteByIdTch']),
  }
}
</script>

<style scoped>
  .icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  .dialog .el-input{
    width:86%;
  }
  .dialog-footer{
    text-align:right;
  }
  a{
    display:inline-block;
    width:50px;
  }
  b{
    display:inline-block;
    width:100px;
  }
</style>


