<!--  -->
<template>
	<div class="student">
		<el-row>
			<el-col :span='16'>
				<el-form inline :model='stu'>
          <el-row>
            <el-col :span='6'>
              <el-form-item>
                <el-select v-model="stu.grade" placeholder="" size='mini'>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-select v-model="stu.clazz" placeholder="" size='mini'>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-input v-model='stu.keys' size='mini' placeholder=''></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-button type='info' size='mini'></el-button>
              </el-form-item>
            </el-col>
          </el-row>
				</el-form>
			</el-col>
			<el-col :span='8'>
				<el-row type='flex' justify='end'>
          <el-form inline>
            <el-row>
              <el-col :span='12'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='single3'></el-button>
                </el-form-item>
              </el-col>
              <el-col :span='12'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='batch3'></el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="student" style="width:100%" height="426" class='table' :header-row-style="rowClass">
      <!-- <el-table-column prop="no" label="" width="100">
      </el-table-column> -->
      <el-table-column prop="stu_account" label="">
      </el-table-column>
      <el-table-column prop="stu_name" label="">
      </el-table-column>
      <el-table-column prop="stu_number" label="">
      </el-table-column>
      <el-table-column prop="stu_gender" label="">
      </el-table-column>
      <!-- <el-table-column prop="num" label="">
        </el-table-column>     -->  
      <el-table-column prop="min_heart" label="">
      </el-table-column>
      <el-table-column prop="avg_heart" label="">
      </el-table-column>
      <el-table-column prop="max_heart" label="">
      </el-table-column>
      <el-table-column prop="first_heart" label="">
      </el-table-column>
      <el-table-column prop="second_heart" label="">
      </el-table-column>
      <el-table-column prop="third_heart" label="">
      </el-table-column>
      <el-table-column prop="stu_height" label="">
      </el-table-column>
      <el-table-column prop="stu_weight" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit3(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete3(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title3" :visible.sync="visible3" center>
      <el-form :model="form3" class='dialog' size='small'>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.no" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stu_account" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stu_number" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stu_name" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-select v-model="form3.stu_grade" placeholder="">
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-select v-model="form3.stu_clazz" placeholder="">
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stu_num" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.avg_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.min_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.max_height" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.first_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.second_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.third_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stu_height" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stu_weight" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-radio class="radio" v-model="form3.stu_gender" label=""></el-radio>
              <el-radio class="radio" v-model="form3.stu_gender" label=""></el-radio>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-radio class="radio" v-model="form3.stu_state" label=""></el-radio>
              <el-radio class="radio" v-model="form3.stu_state" label=""></el-radio>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3"> </el-button>
        <el-button type="info" @click="submit3"> </el-button>
      </div>
    </el-dialog>

    <el-dialog :title="title3s" :visible.sync="visible3s" center>
      <el-form :model="form3s" class='dialog3' size='small'>
        <el-form-item label="" :label-width="fw">
          <el-input placeholder="" suffix-icon="el-icon-document" v-model="form3s.file"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3s"> </el-button>
        <el-button type="info" @click="submit3s"> </el-button>
      </div>
    </el-dialog> 
	</div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';

export default {
  name: 'student',
  data(){
    return {
      fw:'100px',
    	stu:{},
      title3:'',
      title3s:'',
      form3:{},
      form3s:{},
      visible3:false,
      visible3s:false,
    }
  },
  created(){
    this.findAllStu();
  },
  computed:{
    ...mapGetters(['student']),
  },
  methods:{
    edit3(i,row){
      this.visible3=true;
      this.title3='';
      this.form3 = row;
    },
    delete3(i,row){
      var id = row.id;
      this.deleteByIdStu(id).then((result)=>{
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: ''
        });
      });
    },
    single3(){
      this.visible3=true;
      this.title3='';
      this.form3 = {stu_gender:''};
    },
    batch3(){
      this.visible3s=true;
    },
    submit3(){
      this.saveStu(this.form3).then((result)=>{
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch((error)=>{
        console.log(error);
        this.$message({
          type: 'error',
          message: ''
        });
      });
      this.visible3=false;
    },
    submit3s(){
      this.saveStu(this.form3s).then((result)=>{
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: ''
        });
      });
      this.visible3s=false;
    },
    cancle3(){this.visible3=false;},
    cancle3s(){this.visible3s=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
    ...mapActions(['findAllStu','saveStu','deleteByIdStu']),
  },
}
</script>

<style scoped>
  .icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


