<!--   -->
<template>
	<div class="gradeSetting">
		<el-row type='flex' justify='end'>
			<el-button type='info' size='small' @click='add3'></el-button>
		</el-row>
    <el-table :data="gradeData" style="width: 100%" height="450" class='table' stripe :header-row-style="rowClass">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="sName" label="">
      </el-table-column>
       <el-table-column prop="state" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit3(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete3(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title3" :visible.sync="visible3" center>
      <el-form :model="form3" class='dialog'>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form3.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form3.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form3.sName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form3.yes" label=""></el-radio>
          <el-radio class="radio" v-model="form3.yes" label=""></el-radio>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form3.state" label=""></el-radio>
          <el-radio class="radio" v-model="form3.state" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3"> </el-button>
        <el-button type="info" @click="submit3"> </el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script type="text/javascript">
export default {
  name: 'gradeSetting',
  data(){
    return {
      fw:'120px',
      title3:'',
      form3:{},
      visible3:false,
      gradeData: [{
        name:'',
        sName: '',
        state:'',
      },{
        name:'',
        sName: '',
        state:'',
      },{
        name:'',
        sName: '',
        state:'',
      }],
    }
  },
  methods:{
    add3(){this.visible3=true;this.title3='';},
    edit3(i,row){this.visible3=true;this.title3='';},
    delete3(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    submit3(i,row){this.visible3=false;
      this.$message({
        message: '',
        type: 'success',
      });},
    cancle3(){this.visible3=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },

  },
}
</script>

<style scoped>
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  .table{
    overflow:auto;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


