<template>
  <div class="err">
    <div class="top">
      <el-row>
        <el-col :span='5'>
          <el-date-picker size='mini' v-model="form.time" type="date" placeholder=""></el-date-picker>
  		    <!-- <el-date-picker
            v-model="form.value"
            type="daterange"
            start-placeholder=""
            end-placeholder=""
            default-value="2010-10-01" size="mini">
          </el-date-picker> -->
        </el-col>
        <el-col :span='4'>
          <el-select v-model="form.course" size='mini' placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>  
        </el-col>
        <el-col :span='4'>
  			  <el-select placeholder="" size="mini" v-model="form.options">
  			    <el-option
  			      v-for="item in options"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select>
        </el-col>
        <el-col :span='4'>
  			  <el-select placeholder="" size="mini" v-model="form.options1">
  			    <el-option
  			      v-for="item in options1"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select>
        </el-col>
        <el-col :span='4'>
  			  <el-select placeholder="16:00-16:45" size="mini" v-model="form.options3">
  			    <el-option
  			      v-for="item in options3"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select>
        </el-col>
        <el-col :span='3'>
          <el-button type='info' size='mini'></el-button>
        </el-col>
      </el-row>
    </div>
    <div class="content">
      <el-row>	
      	<el-col :span="24">
          <el-row type='flex' justify='center'>
            <span class='title'></span>
          </el-row>
        </el-col>
			</el-row>				
      <el-row class='rowErr'>
			  <el-col :span="2" class='rowErr'></el-col>
			  <el-col :span="2" class='rowErr'><div>3</div></el-col>
			  <el-col :span="2" class='rowErr'></el-col>
			  <el-col :span="2" class='rowErr'><div>4</div></el-col>
        <el-col :span="16">
          <el-row type='flex' justify='end'>
            <el-button type="info" size='mini' class='download' @click='download'></el-button>
          </el-row>
        </el-col>
			</el-row>				
    </div>
    <el-table :data="errs" :span-method="rc" border style="width: 100%;" class='tableErr' stripe :header-row-style="rowClass">
      <el-table-column prop="no" label=""></el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="gender" label="">
      </el-table-column>
      <el-table-column prop="errState" label=""></el-table-column>
      <el-table-column prop="errStart" label=""></el-table-column>
      <el-table-column prop="errSeries" label=""></el-table-column>
      <el-table-column prop="course" label=""></el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name:'err',
  data(){
    return {
      form:{},
      errs:[{
        no: '20170101',
        name: '',
        gender:'',
        errState:'',
        errStart:'14:37:35',
        errSeries:'4',
        course:'',
        content:'',
      }, {
        no: '20170102',
        name: '',
        gender:'',
        errState:'',
        errStart:'14:37:35',
        errSeries:'4',
        course:'',
        content:'',
      }, {
        no: '20170103',
        name: '',
        gender:'',
        errState:'',
        errStart:'14:37:35',
        errSeries:'4',
        course:'',
        content:'',
      }],
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      },{
      	value: '4',
        label: ''
      }],
      options1: [{
        value: '1',
        label: '1'
      }, {
        value: '2',
        label: '2'
      }, {
        value: '3',
        label: '3'
      },{
      	value: '4',
        label: ''
      }],
      options2: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      },{
      	value: '4',
        label: ''
      }],
      options3: [{
        value: '1',
        label: '14:00-14:25'
      }, {
        value: '2',
        label: '15:00-15:45'
      }, {
        value: '3',
        label: '16:00-16:45'
      }],
    }
  },
  methods:{
    rc2({row,column,rowIndex,columnIndex}){//
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1,2];
        } else if (columnIndex === 1) {
          return [0,0];
        }
      }
    },
    rc({row,column,rowIndex,columnIndex}){
      if (columnIndex===0||columnIndex===1||columnIndex===2){  //1230
        if (rowIndex%2===0) {//
          return [2,1];
        } else {
          return [0,0];
        }
      }
    },
    download(){
      this.$message({
        message:'',
        type:'success',
      });
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .el-row {
  	margin-top: 10px;
    margin-bottom: 0px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 5px;
  }
  .title{
    font-size:20px;
  }
  .rowErr{
    height:48px;
    line-height:48px;
  }
  .download{
    margin-bottom:10px;
  }

</style>



